.tool {
  width: 47px;
  border-right: 2px #555 solid;
  background-color: #333;

  &:global(.active) {
    background-color: #444;
  }
}

.toolBtn {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  border: 0;
  background-color: rgba($color: #000000, $alpha: .0);
  color: white;
  cursor: pointer;
}

.icon {
  filter: invert(64%);
}